@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: var(--font);
}

:root {
  /* Font - 500, 700 */
  --font: "Epilogue", sans-serif;

  /* Colors */
  --white: hsl(0, 0%, 100%);
  --almost-white: hsl(0, 0%, 98%);
  --medium-gray: hsl(0, 0%, 41%);
  --almost-black: hsl(0, 0%, 8%);
}

body {
  background-color: var(--almost-white);
}

/* ============================== Navbar ============================== */

.navBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* ========= Left side ========= */

.logo {
  margin: 2rem;
}

.left-side {
  display: flex;
  width: 50%;
}

.left-side ul {
  list-style: none;
  display: flex;
}

.left-side a {
  text-decoration: none;
  color: var(--medium-gray);
  font-weight: 700;
}

.left-side a:hover {
  color: var(--almost-black);
}

.left-side li {
  margin: 2rem;
}

/* ============================== Navbar Dropdowns ============================== */
/* ========= Features Dropdown ========= */
.menu {
  position: absolute;
  height: 11.5rem;
  width: 10rem;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  justify-content: center;
  transform: translate(-2.2rem, 4.5rem);
  background-color: var(--white);
  box-shadow: 0 0.5rem 3rem -1.5rem var(--medium-gray);
}

.arrow:hover {
  fill: var(--almost-black);
}

.icon {
  margin: 0 0.5rem 0 1rem;
}

.menu li {
  margin: 0.6rem;
}

/* ========= Company Dropdown ========= */

.menu-two {
  position: absolute;
  height: 9rem;
  width: 10rem;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  justify-content: center;
  transform: translate(11.2rem, 4.5rem);
  background-color: var(--white);
  box-shadow: 0 0.5rem 3rem -1.5rem var(--medium-gray);
}

.menu-two li {
  margin: 0.6rem 0.6rem 0.6rem 2rem;
}

/* ========= Right side ========= */

.right-side {
  display: flex;
  width: 50%;
  justify-content: flex-end;
}

.right-side ul {
  list-style: none;
  display: flex;
}

.right-side li {
  margin: 2rem;
}

.right-side a {
  text-decoration: none;
  color: var(--medium-gray);
  font-weight: 700;
}

.right-side a:hover {
  color: var(--almost-black);
}

.right-side .register {
  width: 8rem;
  height: 3rem;
  border: 0.15rem solid var(--medium-gray);
  padding: 0.8rem 1.6rem 0.8rem 1.6rem;
  border-radius: 1rem;
}

.right-side .register:hover {
  border-color: var(--almost-black);
}

/* ============================== Mobile Nav ============================== */

.mobile-nav-icon {
  display: none;
}

.top-side {
  display: none;
}

.bottom-side {
  display: none;
}

/* ============================== Main Section ============================== */
.main-section {
  display: flex;
  padding: 0 5rem;
}

/* ========= Info ========= */
.info {
  width: 50%;
  padding: 5rem 5rem 0 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.info h1 {
  color: var(--almost-black);
  font-size: 6rem;
  padding-right: 6rem;
  margin-top: 1rem;
}

.info p {
  color: var(--medium-gray);
  line-height: 2.2rem;
  font-size: 1.5rem;
  padding: 0 6rem 0 0;
}

.learn {
  border-style: none;
  width: 12rem;
  height: 3.8rem;
  font-size: 1.4rem;
  border-radius: 1rem;
  background-color: var(--almost-black);
  color: var(--almost-white);
  cursor: pointer;
}

.learn:hover {
  background-color: var(--almost-white);
  color: var(--almost-black);
  border: 0.15rem solid var(--almost-black);
}

.graphics {
  display: inline-flex;
  justify-content: space-between;
  padding-right: 6rem;
}

.graphics img {
  scale: 0.8;
}

/* ========= Main Graphic ========= */
.main-graphic {
  padding: 5rem;
  width: 50%;
}

.main-graphic img {
  width: 42rem;
  height: 50rem;
}

.attribution {
  text-align: center;
}

/* ============================== Media Queries ============================== */
@media screen and (max-width: 1000px) {
  /* ========= Left side ========= */
  .left-side {
    display: none;
  }

  /* ========= Right side ========= */
  .right-side {
    display: none;
  }

  /* ============================== Mobile Nav ============================== */
  .navBar {
    margin: 1rem 0;
  }

  .mobile-nav-icon {
    display: flex;
    margin: 1rem;
    cursor: pointer;
    z-index: 4;
  }

  .mobile-nav-visible {
    position: fixed;
    left: 77%;
    top: 0%;
    transform: translateX(14.5rem);
    background-color: var(--white);
    width: 14.5rem;
    height: 100%;
    padding: 1rem;
  }

  .mobile-nav-hidden {
    position: fixed;
  }

  .logo {
    margin: 1rem;
  }

  /* ========= Top side ========= */
  .top-side {
    display: flex;
    margin-left: 1rem;
    margin-top: 3.5rem;
  }

  .top-side ul {
    list-style: none;
  }

  .top-side li {
    margin: 1.5rem 2rem 1rem 0;
  }

  .top-side a {
    color: var(--medium-gray);
    text-decoration: none;
  }

  .top-side a:hover {
    color: var(--almost-black);
  }

  .mobile-menu-two li {
    margin-left: 1rem;
  }

  /* ========= Bottom side ========= */
  .bottom-side {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 6rem;
    padding-right: 4rem;
  }

  .bottom-side ul {
    list-style: none;
  }

  .bottom-side li {
    margin-top: 1.5rem;
  }

  .bottom-side a {
    color: var(--medium-gray);
    text-decoration: none;
  }

  .bottom-side a:hover {
    color: var(--almost-black);
  }

  .bottom-side .register {
    width: 8rem;
    height: 3rem;
    border: 0.15rem solid var(--medium-gray);
    padding: 0.6rem 3.5rem 0.6rem 3.5rem;
    border-radius: 1rem;
  }
}

@media screen and (max-width: 768px) {
  .mobile-nav-visible {
    position: fixed;
    left: 50%;
    top: 0%;
    width: 75%;
    padding: 1rem;
    height: 100%;
    background-color: var(--white);
    transform: translateX(-3rem);
    z-index: 2;
  }

  .mobile-nav-hidden {
    position: fixed;
    left: 51.5%;
    top: 0%;
    padding: 1rem;
    height: 100%;
    transform: translateX(-3rem);
  }

  .opacity-filter {
    position: fixed;
    top: 0%;
    right: 100%;
    background-color: hsla(0, 0%, 0%, 0.5);
    width: 65%;
    height: 100%;
  }

  .top-side {
    margin-top: 3.5rem;
  }

  .bottom-side {
    padding-right: 2.8rem;
  }

  /* ============================== Main Section ============================== */
  .main-section {
    flex-direction: column-reverse;
    padding: 0;
  }

  .main-graphic {
    width: 100%;
    padding: 0;
  }

  .main-graphic img {
    width: 100%;
    height: 100%;
  }

  .info {
    width: 100%;
    padding: 1rem;
    align-items: center;
  }

  .info h1 {
    font-size: 2.2rem;
    padding-right: 0;
  }

  .info p {
    font-size: 1rem;
    text-align: center;
    padding: 1rem 0;
  }

  .learn {
    margin-bottom: 3rem;
    width: 11rem;
    height: 3.5rem;
    font-size: 1.3rem;
  }

  .graphics {
    padding-right: 0;
  }

  .graphics img {
    scale: 0.7;
  }

  .attribution {
    margin-top: 3rem;
    font-size: 0.9rem;
    text-align: center;
  }
}
